@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
:root {
  --black: #161a2b;
  --white: #ffffff;
}

body {
  text-align: center;
  background-color: rgb(74, 57, 189);
  background-image: linear-gradient(to right,
  rgba(48, 16, 255, 1) 0%,
  rgb(206, 116, 74) 21%,
  rgb(219, 74, 74) 50%,
  rgb(207, 61, 200) 79%,
  rgba(48, 16, 255, 1) 100%
  );
}

/* Navbar */

.navbar-color{
  background-color: #161a2b;
  background-color: var(--black);
}

.navbar-icons{
  font-size: 24px;
  padding: 2px 8px;
}
* {
	box-sizing: border-box;
}


/* Blog */


.main-blog-div {
  display: inline-table;
  flex-direction: column;
  justify-content: center;
  opacity: 1;
  /* min-width: 400px; */
  width: 92%;
  max-width: 750px;
  height: auto;
  background: #161a2b;
  background: var(--black);
  text-align: center;
  margin: 20px 20px;
  border-radius: 10px;
  padding: 10px 10px 25px 10px;
  -webkit-animation: fadeIn 0.5s 1;
          animation: fadeIn 0.5s 1;
}

.blog-title {
  font-size: 45px;
  font-weight: bold;
  color: rgba(255, 84, 17, 1);
	letter-spacing: 1px;
	padding: 10px 15px;
}

.blog-subheading{
  font-size: 30px;
  font-weight: bold;
  color: rgba(17, 122, 255, 1);
	letter-spacing: 1px;
	padding: 10px 15px;
}

.blog-para {
  font-family: 'Muli', sans-serif;
  text-align: left;
  font-size: 20px;
  color: #ffffff;
  opacity: 0.9;
  padding: 6px 30px;
	letter-spacing: 2px;
}

.blog-image-div{
  display: flex;
  justify-content: center;
  align-items: center;
}

.blog-list{
  justify-content: left;
  text-align: left;
  font-size: 20px;
  opacity: 0.9;
  padding: 10px 30px 10px 35px;
  color: #ffffff;
}

/* .blog-list-ul{

} */

.redirect-button{
  padding: 16px;
  border: none;
  border-radius: 5px;
  margin: 10px auto;
  cursor: pointer;
  outline: none;
  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  color: #fff;
}

.blog-image{
  border: 2px solid rgba(255, 118, 20, 1);
  border-radius: 10px;
  margin: 10px 10px 15px 10px;
  max-height: 350px;
  width: auto;
  max-width: 90%;
  height: auto;
}


/* Home Page Cards */

.cards-box {
	background-color: #161a2b;
	background-color: var(--black);
	border-radius: 10px;
	box-shadow: 0 10px 10px rgba(0, 0, 0, 0.3);
  display: inline-table;
  /* min-width: 400px; */
	max-width: 400px;
  margin: 20px;
  min-height: 220px;
  cursor: pointer;
  -webkit-animation: fadeIn 0.5s 1;
          animation: fadeIn 0.5s 1;
}


.cards-box:hover{
  border-radius: 10px;
  transition: transform .5s;
  transform: scale(1.05);
}


.cards-info {
  padding: 3px 5px 10px 5px;
  width: 100%;
}


.cards-title {
  font-size: 28px;
  font-weight: bold;
  color: rgba(255, 84, 17, 1);
	letter-spacing: 1px;
	padding: 10px 5px;
}


.cards-description {
  font-size: 18px;
  font-family: 'Muli', sans-serif;
  color: #ffffff;
  opacity: 0.8;
  padding: 4px 10px;
	letter-spacing: 2px;
}


@-webkit-keyframes fadeIn {
	0% {opacity: 0;}
	100% {opacity: 1;}
}


@keyframes fadeIn {
	0% {opacity: 0;}
	100% {opacity: 1;}
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

body {
  text-align: center;
  background-color: rgb(74, 57, 189);
  background-image: linear-gradient(to right,
  rgba(48, 16, 255, 1) 0%,
  rgb(206, 116, 74) 21%,
  rgb(219, 74, 74) 50%,
  rgb(207, 61, 200) 79%,
  rgba(48, 16, 255, 1) 100%
  );
}


.main-outertodo-div{
  -webkit-animation: fadeIn 0.5s 1;
          animation: fadeIn 0.5s 1;
}


.todo-app {
  position: relative;
  display: inline-table;
  flex-direction: column;
  justify-content: start;
  min-width: 400px;
  width: 28%;
  height: 550px;
  background: #161a2b;
  text-align: center;
  margin: 12px 20px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  padding-bottom: 32px;
}

.extra-padding {
  padding-bottom: 95px;
}

@media only screen and (max-width: 435px){
  .todo-app {
    min-width: 90vw;
  }
}


h1 {
  margin: 20px 0px 15px 0px;
  color: rgb(4, 238, 4);
  font-family: cursive ;
  font-size: 30px;
  font-weight: bold;
}

h2 {
  margin: 20px 0;
  color: #fff;
  font-size: 24px;
}


.todo-form {
  margin-bottom: 32px;
  margin-left: 4px;
  margin-right: 4px;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #5d0cff;
  outline: none;
  width: 70%;
  background: transparent;
  color: #fff;
}

.todo-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.todo-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.todo-input::-ms-input-placeholder {
  color: #e2e2e2;
}

.todo-input::placeholder {
  color: #e2e2e2;
}

.todo-button {
  padding: 16px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  color: #fff;
  text-transform: capitalize;
}
.todo-input.edit {
  border: 2px solid #149fff;
}

.todo-button.edit {
  padding: 16px 22px;
}

.edit-form{
  margin-bottom: 15px;
}

.delete-all-button{
  position: absolute;
  bottom: 0%;
  transform: translate(-50%, -50%);

  padding: 16px;
  border: none;
  border-radius: 5px;
  /* margin: 10px auto; */
  cursor: pointer;
  outline: none;
  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  color: #fff;
  text-transform: capitalize;
  -webkit-animation: fadeIn 1s 1;
          animation: fadeIn 1s 1;
}


@media only screen and (max-width: 365px){
  .todo-input{
    border-radius: 4px;
    margin-top: 4px;
  }

  .todo-button{
    border-radius: 4px;
    margin-top: 4px;
  }
}


.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: #fff;
  word-break: break-all;
  padding: 16px;
  border-radius: 5px;
  width: 90%;
  cursor: -webkit-grab;
  cursor: grab;
}


.todo-row.orange{
  background: linear-gradient(
    90deg,
    rgba(255, 118, 20, 1) 0%,
    rgba(255, 84, 17, 1) 100%
  );
}


.todo-row.blue{
  background: linear-gradient(
    90deg,
    rgba(20, 159, 255, 1) 0%,
    rgba(17, 122, 255, 1) 100%
  );
  
}


.todo-row.red{
  background: linear-gradient(
    90deg,
    rgb(255, 20, 79) 0%,
    rgb(238, 44, 92) 100%
  );
}


.todo-row.pink{
  background: linear-gradient(
    90deg,
    rgba(255, 12, 241, 1) 0%,
    rgba(250, 0, 135, 1) 100%
  );
}


.todo-row.purple{
  background: linear-gradient(
    90deg,
    rgb(87, 5, 253) 0%,
    rgb(129, 8, 204) 100%
  );
}


.todo-row.green{
  background: linear-gradient(
    90deg,
    rgb(93, 187, 49) 0%,
    rgb(84, 202, 29) 100%
  );
}


.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.edit-icon {
  /* margin-right: 5px; */
  color: #fff;
}

.delete-icon {
  color: #fff;
}


.color-select-div{
  display: inline-table;
  margin: 5px;
  width: 150px;
  max-width: 150px;
  cursor: pointer;
  text-align: center;
  align-items: center;
  -webkit-animation: fadeIn 0.7s 1;
          animation: fadeIn 0.7s 1;
}

@media only screen and (max-width: 297px){
  .color-select-div{
    margin-bottom: 0px;
  }
}

@-webkit-keyframes fadeIn {
	0% {opacity: 0;}
	100% {opacity: 1;}
}

@keyframes fadeIn {
	0% {opacity: 0;}
	100% {opacity: 1;}
}

:root {
    --black: #161a2b;
    --white: #ffffff;
}



.main-outer-div{
    margin: 20px;
    padding-top: 10px;
    min-height: 85vh;
    min-width: 1200px;
}

.form-div{
    padding-bottom: 0px;
    /* border-bottom: 3px solid rgba(255, 84, 17, 1); */
}
  
  
.tree-outer-div{
    max-width:-webkit-fit-content;
    max-width:-moz-fit-content;
    max-width:fit-content;
    margin: auto;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding-top: 20px;
}
  
  .tree-main-div{
    text-align: center;
    width:-webkit-max-content;
    width:-moz-max-content;
    width:max-content;
    overflow: auto;
    justify-content: center;
    align-items: center;
    z-index: 2;
  }
  
.rows{
    display: block;
    margin-top: 40px;
    text-align: center;
}
  
.tree-node-div{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    width: 45px;
    height: 45px;
    /* max-width: 40px; */
    background-color: rgba(17, 122, 255, 1);
    background: linear-gradient(
        90deg,
        rgba(20, 159, 255, 1) 0%,
        rgba(17, 122, 255, 1) 100%
    );
    margin: auto;
    color: #fff;
    /* border: 1px solid white; */
    text-align: center;
    border-radius: 25%;
    opacity: 0.99;
}
  
.transparent{
    background-color: transparent;
    background: none;
    border: transparent;
    /* border: 1px solid white; */
    width: 40px;
    height: 30px;
}

.searched{
    background-color: rgb(84, 202, 29)  ;
    background: linear-gradient(
    90deg,
    rgb(93, 187, 49) 0%,
    rgb(84, 202, 29) 100%
  );
}

.bst-form {
    margin-bottom: 32px;
    margin-left: 4px;
    margin-right: 4px;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
    display: inline-flex;
}

.operation-input{
    padding: 12px;
    border-radius: 4px;
    border: 2px solid #5d0cff;
    outline: none;
    width: 80px;
    background: transparent;
    color: #fff;
}


/* Chrome, safari */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.operation-button{
    padding: 14px;
    border: none;
    margin-left: 5px;
    border-radius: 4px;
    cursor: pointer;
    outline: none;
    background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
    );
    color: #fff;
    text-transform: capitalize;
}

.clear-button{
    padding: 14px;
    border: none;
    margin-left: 50px;
    border-radius: 4px;
    cursor: pointer;
    outline: none;
    background: linear-gradient(
        90deg,
        rgb(93, 187, 49) 0%,
        rgb(84, 202, 29) 100%
    );
    color: #fff;
    text-transform: capitalize;
}

.refresh-button{
    padding: 14px;
    border: none;
    margin-left: 5px;
    border-radius: 4px;
    cursor: pointer;
    outline: none;
    background: linear-gradient(
        90deg,
        rgb(93, 187, 49) 0%,
        rgb(84, 202, 29) 100%
    );
    color: #fff;
    text-transform: capitalize;
}

  
